// ðŸŒ• LIGHT (blue) // 

:root {
    // dark/light toggle in app -> 
    --bgcolor: #e5ecf5;
    --dark-shadow: #bec8e4;
    --light-shadow: #ffffff;

    --text-color-primary: rgba(0, 0, 0, 0.93);
    --text-color-secondary: rgba(0, 0, 0, 0.7);
    --text-color-disabled: rgba(0, 0, 0, 0.45);

    // color toggle in app -> 
    --primary: #2978FF;
    --primary-dark: #0151D9;
    --primary-light: #7DADFE;

    --bg-convex: linear-gradient(145deg, var(--light-shadow), var(--dark-shadow));
    --bg-concave: linear-gradient(145deg, var(--dark-shadow), var(--light-shadow));
}

// DYNAMIC GLOBAL VARS for * .SCSS // 

$background-color: var(--bgcolor);
$dark-shadow: var(--dark-shadow);
$light-shadow: var(--light-shadow);

$text-color-primary: Var(--text-color-primary);
$text-color-secondary: var(--text-color-secondary);
$text-color-disabled: var(--text-color-disabled);

$primary: var(--primary);
$primary-dark: var(--primary-dark);
$primary-light: var(--primary-light);

// default . don't change //
$white: #ffffff;
$black: #000000;

$border-color: $dark-shadow;
$border-radius: 0.5rem;
$border-radius-circle: 50%;

// elevation levels 1(low) - 5(high) //
$box-shadow-1: 3px 3px 6px $dark-shadow, -3px -3px 6px $light-shadow;
$box-shadow-2: 6px 6px 12px $dark-shadow, -6px -6px 12px $light-shadow;
$box-shadow-3: 9px 9px 18px $dark-shadow, -9px -9px 18px $light-shadow;
$box-shadow-4: 12px 12px 24px $dark-shadow, -12px -12px 24px $light-shadow;
$box-shadow-5: 15px 15px 30px $dark-shadow, -15px -15px 30px $light-shadow;

// depressed 
$box-shadow-inset: inset -2px -2px 5px $light-shadow, inset 2px 2px 5px $dark-shadow;

// 3D buttons & concave gradients 
$bg-convex: var(--bg-convex);
$bg-concave: var(--bg-concave);

$bg-convex-primary: linear-gradient(145deg, $primary-light, $primary-dark);
$bg-concave-primary: linear-gradient(145deg, $primary-dark, $primary-light);

$normal-font: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;

$transition: 0.125s;
$transition-long: 0.325s;

* {
    user-select: none;
}

.no-transition {
    * {
        transition: none !important;
    }
}

// end //




